.inputField {
    outline: none;
    border: none;
    background-color: transparent;
    padding: 0.3rem 0;
    color: var(--color-primary);
    font-size: var(--font-size-normal);
    flex: 0 0 100%;
}

@media only screen and (max-width: 56.25em) {
    .inputField {
        font-size: 1.65rem;
    }
}

@media only screen and (max-width: 37.5em) {
    .inputField {
        font-size: 1.9rem;
    }
}

.inputFieldContainer {
    display: flex;
    align-items: center;
    font-size: var(--font-size-normal);
    border-bottom: 0.14rem solid var(--color-primary);
}

.inputFieldError {
    display: flex;
    align-items: center;
    font-size: var(--font-size-normal);
    border-bottom: 0.14rem solid var(--color-danger-1);
}

.inputFieldIcon {
    margin-left: -2rem;
    color: var(--color-primary);
    cursor: pointer;
    font-size: var(--font-size-normal);
}

@media only screen and (max-width: 56.25em) {
    .inputFieldIcon {
        font-size: 1.9rem;
    }
}